<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">
      <span>{{title}}</span>
      <i class="iconfont icon-huixingzhen"></i>
    </div>
    <div class="personnal-detail-wrap-head-sub">
      <div
        class="personnal-detail-wrap-head-sub-split fl"
        v-for="(c,i) in subTexts"
        :key="i"
        :class="{'red-text':c.warn}"
      >{{c.text}}</div>
    </div>
    <div class="clear"></div>
    <div class="personnal-detail-wrap-mgt" v-if="edit">
      <el-input
        type="textarea"
        placeholder="请输入内容"
        v-model="textarea"
        maxlength="200"
        rows="18"
        show-word-limit
      ></el-input>
    </div>
    <div class="personnal-detail-wrap-mgt" v-if="edit">
      <el-upload
        class="personnal-detail-wrap-mgt-upload"
        action="https://jsonplaceholder.typicode.com/posts/"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :file-list="fileList"
        list-type="picture"
      >
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>
    </div>
    <div class="personnal-detail-wrap-homework-detail" v-if="!edit">
      <div class="personnal-detail-wrap-homework-detail-title">答题内容</div>
      <ol type="1">
        <li v-for="(q,i) in questions" :key="i">{{q}}</li>
      </ol>
    </div>
    <div class="personnal-detail-wrap-homework-detail" v-if="!edit">
      <div class="personnal-detail-wrap-homework-detail-title">附件详情</div>
      <div class="personnal-detail-wrap-homework-detail-container">
        <div
          class="personnal-detail-wrap-homework-detail-container-item fl"
          v-for="(c,j) in imgs"
          :key="j">
          <img :src="c.url" @click="showFullScreen(c)">
          <image-fullscreen :url="c.url" :show="c.show" @zoomin="onZoomIn(c)"></image-fullscreen>
        </div>
        <div class="clear"></div>
      </div>
    </div>
    <div class="personnal-detail-wrap-mgt">
      <el-button v-if="!edit" type="primary" size="small" @click="back">返回</el-button>
      <el-button v-if="edit" type="primary">创建</el-button>
      <el-button v-if="edit" type="info" plain>取消</el-button>
    </div>
  </div>
</template>
<script>
import imageFullscreen from "../../../../components/imageFullScreen";
export default {
  components: {
    imageFullscreen
  },
  data() {
    return {
      edit: true,
      title: "大学英语最易错的语法+单词+书写之时大汇总",
      subTexts: [
        {
          text: "2019-04-10  15:32:46  发布"
        },
        {
          text: "江丽颜老师"
        },
        {
          text: "英语作业"
        },
        {
          warn: true,
          text: "截止时间：2019-04-12"
        }
      ],
      textarea: "",
      fileList: [
        {
          name: "附件1.jpeg",
          url:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1558413585874&di=6aee855129022488ee7900328aea8151&imgtype=0&src=http%3A%2F%2Fwww.xyjyjt.com%2Fuploads%2Fallimg%2F150523%2F15420K5H-0.jpg"
        }
      ],
      questions: [
        "What room has no walls,no doors,no windows,and no floors?A mushroom.",
        "What is smaller than an insect's mouth?Anything it eats.",
        "What large instrument do you carry in your ears?Drums,that is eardrums",
        "What's too much for one,just right for two,but nothing at all for three?A secret.",
        "What person tried to make you smile most of the time?A photographer.",
        "What animal has a head like a cat,eyes like a cat,a tail like a cat,but isn't a cat?A kitten.",
        "What surprising things happen every 24 hours?Day breaks,but doesn't fall; night falls,but doesn't break.",
        "What can hear you without ears and can answer you without a mouth?"
      ],
      imgs: [
        {
          url:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1558413585874&di=6aee855129022488ee7900328aea8151&imgtype=0&src=http%3A%2F%2Fwww.xyjyjt.com%2Fuploads%2Fallimg%2F150523%2F15420K5H-0.jpg",
          show: false
        }
      ]
    };
  },
  created() {
    let id = this.$route.params.id;
    if (id.split("&").length > 1) {
      this.edit = false;
    } else {
      this.edit = true;
    }
  },
  methods: {
    showFullScreen(c) {
      this.$set(c, "show", true);
    },
    onZoomIn(c) {
      this.$set(c, "show", false);
    },
    back() {
      this.$router.back();
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    }
  }
};
</script>
<style lang="less" scoped>
.personnal-detail-wrap-head-sub {
  width: 666px;
  &-split {
    padding: 0 25px;
    height: 18px;
    line-height: 18px;
    border-right: 1px solid #a3a3a3;
    color: #a3a3a3;
    font-size: 0.9rem;
    &:last-child {
      border-right: 0;
      text-align: right;
    }
    &:first-child {
      text-align: left;
      padding-left: 0;
    }
    &:nth-child(4) {
      width: 33.32%;
    }
    &:after {
      clear: both;
    }
  }
}
.personnal-detail-wrap-mgt {
  margin-top: 28px;
  text-align: left;
  &-upload {
    width: 500px;
  }
}
.personnal-detail-wrap-homework-detail {
  margin-top: 28px;
  &-title {
    margin-bottom: 12px;
  }
  &-title,
  li {
    text-align: left;
    color: #999999;
  }
  &-container {
    width: 100%;
    &-item {
      width: 100px;
      height: 100px;
      margin-right: 28px;
      img {
        width: 100%;
        height: 100%;
        cursor: zoom-in;
      }
    }
  }
}
</style>



