<template>
  <section class="background" v-if="show" @click="onZoomIn">
    <img ref="img" :src="url" :height="height">
  </section>
</template>
<script>
export default {
  props: {
    url: {
      type: String,
      required: true
    },
    show: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      height: null
    };
  },
  watch: {
    show(n) {
      if (n) {
        this.init();
      }
    }
  },
  methods: {
    async init() {
      await this.$nextTick();
      const img = this.$refs.img;
      this.height = img.naturalHeight;
    },
    onZoomIn() {
      this.$emit("zoomin");
    }
  }
};
</script>
<style lang='less' scoped>
.background {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(55, 55, 55, 0.6);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}
img {
  cursor: zoom-out;
}
</style>


